.fireflies-container {
  position: absolute;
  width: 100%;
  height: 100%;
  pointer-events: none; /* Prevents interference with other UI elements */
  z-index: 9999; /* Ensures fireflies are on top */
}

.firefly {
  position: absolute;
  width: 8px; /* Firefly size */
  height: 8px; /* Firefly size */
  background-color: rgba(255, 255, 150, 0.9); /* Yellowish glow */
  border-radius: 50%;
  box-shadow: 0 0 10px rgba(255, 255, 150, 1),
              0 0 20px rgba(255, 255, 150, 0.8),
              0 0 30px rgba(255, 255, 150, 0.6); /* Glowing effect */
}